import { Component, OnDestroy   } from '@angular/core';
import { NbThemeService } from '@nebular/theme';

import { ElectricityService } from '../../../@core/data/electricity.service';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {DecimalPipe} from "@angular/common";

@Component({
  selector: 'ngx-electricity',
  styleUrls: ['./electricity.component.scss'],
  templateUrl: './electricity.component.html',
  providers: [DecimalPipe]
})
export class ElectricityComponent implements OnDestroy {

  data: Array<any>;

  type = '3 ORE';
  types = ['3 ORE', 'GIORNO', 'SETTIMANA'];

  currentTheme: string;
  themeSubscription: any;
  stats = {
    minTemperature: 0.0,
    maxTemperature: 0.0,
    avgTemperature: 0.0
  };

  date = new Date();



  constructor(private eService: ElectricityService, private themeService: NbThemeService, private http: HttpClient, private decimalPipe: DecimalPipe) {
    this.data = this.eService.getData();

    this.themeSubscription = this.themeService.getJsTheme().subscribe(theme => {
      this.currentTheme = theme.name;
    });


    // headers.append('Authorization', 'Basic ' + btoa(username + ':' + password));
    // headers.set("Content-Type", "application/x-www-form-urlencoded");

    /* this.data = points.map((d, index) => ({
       label: d ,
       value: index,
     }));*/
    this.loadChart(this.type);

    // this.stats = {};
     // this.stats.avgTemperature = 23.45678;


  };

  loadChart(t: string){
    this.type = t;



    let username: string = 'admin';
    let password: string = '87654321';
    let headers = new HttpHeaders();


    this.date = new Date();


    if(t == '3 ORE'){
     this.date.setHours(this.date.getHours() - 3);
    }
    else if(t == 'GIORNO'){
      this.date.setDate(this.date.getDate() - 1 );
    }
    else if (t== 'SETTIMANA'){
      this.date.setDate(this.date.getDate() - 7 );
    }



    this.http.get('https://graziano.herokuapp.com/thermostat/measurementsStats?dateStart=' + this.date.getTime() + '&sensor_id=11', {headers: headers}).subscribe((data:any) => {
      this.stats = data;
    });

  }

  ngOnDestroy() {
    this.themeSubscription.unsubscribe();
  }
}



