import {Component, OnDestroy, OnInit} from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Observable} from 'rxjs/Rx';

@Component({
  selector: 'ngx-temperature',
  styleUrls: ['./temperature.component.scss'],
  templateUrl: './temperature.component.html',
})
export class TemperatureComponent implements OnDestroy, OnInit {

  currentTemperature = 0;
  temperature: number;
  temperatureOff = false;
  temperatureMode = 'cool';
  tempMax= 30;
  tempMin = 10;


  colors: any;
  themeSubscription: any;

  sensorDefault = '0';

  dividedButtonGroupTwo = {
    left: false,
    middle: false,
    right: false,
  };

  sensors = [];


  constructor(private theme: NbThemeService, private http: HttpClient) {
    this.temperature = 25;
    this.themeSubscription = this.theme.getJsTheme().subscribe(config => {
      this.colors = config.variables;
    });
  }

  ngOnInit(){
    let username: string = 'admin';
    let password: string = '87654321';
    let headers = new HttpHeaders();
    // headers.append('Authorization', 'Basic ' + btoa(username + ':' + password));
    // headers.set("Content-Type", "application/x-www-form-urlencoded");

    this.http.get('https://graziano.herokuapp.com/thermostat/sensors?thermostat_id=7', {headers: headers}).subscribe((data:any) => {
      // Read the result field from the JSON response.
;
      this.sensors = data;
    });

    this.http.get('https://graziano.herokuapp.com/thermostat/lastMeasurements?sensor_id=11', {headers: headers}).subscribe((data:any) => {
      // Read the result field from the JSON response.
      console.log(data);
      this.currentTemperature = data.temperature;
    });

    Observable.interval(5000).subscribe(x => {
      this.http.get('https://graziano.herokuapp.com/thermostat/lastMeasurements?sensor_id=11', {headers: headers}).subscribe((data:any) => {
        // Read the result field from the JSON response.
        console.log(data);
        this.currentTemperature = data.temperature;
      });
    });


  }

  ngOnDestroy() {
    this.themeSubscription.unsubscribe();
  }

  increaseTemp() {
    if (this.temperature < this.tempMax && !this.temperatureOff) {
      this.temperature += 0.5;
    }
  }

  decreaseTemp(){
    if (this.temperature > this.tempMin && !this.temperatureOff) {
      this.temperature -= 0.5;
    }
  }


  changeTemp() {
   //np console.log(this.temperatureMode);
  }
}



